$primary-color: #00b494;
$border-color: #E5E6E7;
$font: 'Vazir', sans-serif;

$mailHeaderColor: #696C6E;

.mail-list-header {
    padding: 20px;
    border-bottom: 1px solid $border-color;
    
    .mail-list-header__row {
        display: grid;
        align-items: center;
        grid-template-columns: 25% 1fr 40%;
    
        h2 {
            color: $mailHeaderColor;
            font-weight: normal;
        }
    
        &__actions {
            display: flex;
        }

        &__page {
            display: flex;
            justify-content: flex-end;
            grid-column-start: 3;
            grid-column-end: 4;
        }
    }
}