$primary-color: #00b494;
$border-color: #E5E6E7;
$font: 'Vazir', sans-serif;

$navigator-background: #f3f3f4;
.navigator {
    height: 100vw;
    background-color: $navigator-background;
    padding: 20px;

    &__labels {
        &__wrapper {
            display: flex;
            flex-wrap: wrap;
        }
    }
}